import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { executeScroll } from '_includes/js/scroll.js'
import './styles.sass'

class ScrollToNext extends Component{

  static contextTypes = {
    theme: PropTypes.any
  }

  ScrollFunction(){
    const SELECTOR = this.props.nextPageSelector
    const PAGE_FROM_SELECTOR = document.querySelector(SELECTOR)
    executeScroll(PAGE_FROM_SELECTOR)
  }



  render(){
    return(
      <div className="to-next">
        <div className="scroll-icon" onClick={(e) => this.ScrollFunction()}>
          <span style={{color: this.context.theme.text}}>Click to scroll</span>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" style={{fill: this.context.theme.text}}>
            <path d="M143 352.3L7 216.3c-9.4-9.4-9.4-24.6 0-33.9l22.6-22.6c9.4-9.4 24.6-9.4 33.9 0l96.4 96.4 96.4-96.4c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9l-136 136c-9.2 9.4-24.4 9.4-33.8 0z"/>
          </svg>
        </div>
      </div>
    )
  }
}

export default ScrollToNext



